@import url(https://fonts.googleapis.com/css2?family=Pangolin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


#navbar{
    /* background-color: rgba(217, 217, 217, .9); */
    background: transparent;
    font-family: "Montserrat", sans-serif
}

#navLinks Button{
    background: transparent;
    border: none;
    color: whitesmoke;
}

#navLinks Button:hover{
    background-color: whitesmoke;
    color: black;
}

#brand span{
    color: rgb(164, 168, 161);
}

.mainDiv{
    background-image: url(/static/media/landing.36dde3a5.jpg);
    background-size: cover;
    background-repeat: no-repeat;   
    width: 100vw;
    height: 100vh;
    margin-top: -5%;
}

.navButtons button{
    width: 160px;
    height: 40px;
    border-radius: 10px;
    font-family: "Montserrat", sans-serif;
    background-color: black;
    box-shadow: 0px 3px 5px rgb(187, 187, 187);
    margin-top: 20px;
    margin-left: 36px;
    color: rgb(199, 199, 199);
    text-shadow: 1px 1px black;
    font-size: 18px;
}

.navButtons{
    justify-content: center;
}

.navButtons button:hover{
    background-color: white;
    color: black;
    text-shadow: 1px 1px rgb(129, 129, 129);

}

.title{
    color: white;
    font-family: 'Lobster', cursive;
    /* font-family: 'Pangolin', cursive;  */
    font-size: 100px;
    padding: 0;
    margin: 0;
    text-shadow: 2px 2px 3px black;

}

.title span{
    font-family: 'Pangolin', cursive;
    color: rgb(144, 218, 83);
}

